<template>
<div v-if="GameModeState.data != null" id="pictureFirstParent">
    <product-head 
    :isDefaultGuy="(GameModeState.rightArticle != GameModeState.selectedArticle) ? GameModeState.isHidden : true" 
    :productCategory="GameModeState.data.products[GameModeState.data.rightProductID].productCategory"
    class="productHead"/>

    <div id="productsParent">
        <div id="currentProductImageParent">
            <img id="currentProductImage" :src="GameModeState.data.products[GameModeState.data.rightProductID].imageURL" draggable="false"/>
        </div>
        <div id="productNameListParent"> 
            <selectable-proudct 
            class ="productElement"
            v-for="product in GameModeState.data.products"
            :key="product.article"
            :isSelected="GameModeState.selectedArticle === product.article" 
            :productUrl="product.productURL"
            :productName="product.productName"
            :productCategory="product.productCategory"
            :isHidden="GameModeState.isHidden"
            :isRight="GameModeState.rightArticle === product.article"
            :isWrong="GameModeState.wrongArticle === product.article"
            @click="productItemClicked(product)"
            />
        </div>
    </div>

    <div id="WrongOrRight">
        <span :class="WrongOrRightStyle">
            {{WrongOrRightText}}
        </span>
    </div>


    <div id="buttons">
        <button 
        class="blueButton btn" 
        :disabled="GameModeState.isApplyButtonDisabled"
        @click="applyChoice"> 
            {{ $t('nameFirst.CheckIt')}}
        </button>

        <button 
        class="whiteButton btn" 
        :disabled='GameModeState.isNextRoundButtonDisabled'
        @click="nextRound"> 
            {{ $t('nameFirst.Next')}}
        </button>
    </div>

    <div id="statisticParent">
        <div>
            <span class="fontBlue">{{ $t('nameFirst.CurrentScore')}}</span>
            <span>{{GameModeState.currentScore}}</span>
        </div>
        <div>
            <span class="fontBlue">{{ $t('nameFirst.HighScore')}}</span>
            <span>{{isAuth ? authorizedPfBestscore : GameModeState.highScore}}</span>
        </div>
    </div>


</div>
</template>

<script setup>
import productHead from './components/Header.vue';
import selectableProudct from './components/SelectableProduct.vue';
import { computed } from 'vue';
import { useGameMode } from '@/services/gameModeServices/gameMode.js'
import { useIncrementPictureFirstAccountScore } from '@/services/bestscoresServices/bestscores.js'
import store from '@/store/index.js'

const isAuth = computed(() => store.state.user.isAuthorized);
const authorizedPfBestscore = computed(() => store.state.user.pf_bestscore);

const setSelectedSideMenuId = () => {
    store.commit('menus/setSelectedId', 2);
}

const { GameModeState, WrongOrRightText, WrongOrRightStyle, productItemClicked, applyChoice, nextRound } = useGameMode({
    incrementAccountScore: useIncrementPictureFirstAccountScore,
    setSelectedId: setSelectedSideMenuId,
    authorizedBestscore: authorizedPfBestscore,
})

</script>

<style lang="scss" scoped>
@import "@/styles/GameMode/GameMode.scss";

#pictureFirstParent{
    display: flex;
    flex-direction: column;
    margin-left: 0px;
}

#productsParent {
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
    display: flex;
}

#currentProductImage {
    width: 200px;
    height: 200px;
    border: 1px solid;
    border-radius: 10px;
}

.productElement {
    display: flex;
    flex-direction: column;
    width: 375px;
    height: 64px;
}

#productNameListParent {
    margin-left: 25px;
    display: flex;
    height: 202px;
    flex-direction: column;
    justify-content: space-between;
}

@media (max-width: 650px) { 
    #productsParent { 
        flex-direction: column;
    }

    #productNameListParent {
        margin-top: 10px;
        margin-left: auto;
        height: auto;
    }

    #currentProductImageParent {
        display: flex;
    }
    #currentProductImage {
        width: 150px;
        height: 150px;
        margin: 0 auto;
        border: 1px solid;
        border-radius: 10px;
    }

    .productElement {
        margin-bottom: 10px;
    }

}

</style>