export const PopupsModule = {
    state: () => ({
        isLangPopupVisible: false,
        isAuthPopupVisible: false,
        popupTitleCallback: () => {},
        iconUri: require(`@/images/icons/SelectLangIcon.png`),
    }),
    getters: {
        
    },
    mutations: {
        ShowAuthPopup(state) {
            state.isAuthPopupVisible = true;
            state.isLangPopupVisible = false;
            state.iconUri = require(`@/images/icons/authPopupIcon.png`)
        },
        ShowLangPopup(state) {
            state.isLangPopupVisible = true;
            state.isAuthPopupVisible = false;
            state.iconUri = require(`@/images/icons/SelectLangIcon.png`)
        },
        CloseAnyPopup(state) {
            state.isAuthPopupVisible = false;
            state.isLangPopupVisible = false;
        },
        SetPopupTitleCallback(state, callback) {
            state.popupTitleCallback = callback;
        }
    },
    actions: {
        
    },
    namespaced: true,
}