import axiosInstance from '@/axios/axios.js';
import store from '@/store/index.js'

export async function useUserpage(props) { 
    const response = await axiosInstance.get(process.env.VUE_APP_USER_CONTROLLER + "AccountPage", {
        headers: {
            Authorization: "Bearer " + $cookies.get("kafferep_accessToken")
        }
    })

    if(response != null && response.status == 200) {
        props.userData = response.data;

        store.commit('user/SetUser', {
            username: response.data.username,
            userAvatarId: response.data.currentAvatarId,
            usericon: response.data.avatars[response.data.currentAvatarId-1].avatarImageUrl,
            tfScore: response.data.titleFirstBestscore,
            pfScore: response.data.pictureFirstBestscore
        });
    }
}