import axiosInstance from '@/axios/axios.js';
import store from '@/store/index.js'
export async function useLoadBestscores() {
    const bestscores = await axiosInstance.get(process.env.VUE_APP_USER_CONTROLLER + "GetBestscores", {
        headers: {
            Authorization: "Bearer " + $cookies.get("kafferep_accessToken")
        },
    })
    .catch((error) => {

    });

    if(bestscores != null && bestscores.status == 200) {
        store.commit('user/SetBestscores', {
            tf_bestscore: bestscores.data.bestscores.tf_bestscore, 
            pf_bestscore: bestscores.data.bestscores.pf_bestscore
        });
    }
}

export async function useIncrementTitleFirstAccountScore(currentScore) {
    if(currentScore > store.state.user.tf_bestscore) {
        const response = await axiosInstance.post(process.env.VUE_APP_USER_CONTROLLER + "IncrementBestscore", null, {
            headers: {
                Authorization: "Bearer " + $cookies.get("kafferep_accessToken")
            }, 
            params: { 
                mode: "TitleFirst"
            }
        })

        if(response != null && response.status == 200) {
            store.commit('user/IncrementTfBestscore')
        }
    }
}

