import axiosInstance from '@/axios/axios.js';
import store from '@/store/index.js'
export async function useLoadAvatar(onFailCallback) {
    
    const avatarData = await axiosInstance.get(process.env.VUE_APP_USER_CONTROLLER + "GetUserAvatar", {
        headers: {
            Authorization: "Bearer " + $cookies.get("kafferep_accessToken")
        }
    })
    .catch((error) => { });

    if(avatarData != null && avatarData.status == 200) {    
        store.commit('user/SetAvatar', {
            avatarId: avatarData.data.avatar.avatarId,
            avatarUrl: avatarData.data.avatar.avatarImageUrl,
        });
    }
    else {
        onFailCallback();
    }
}

