export const UserModule = {
    state: () => ({
        isAuthorized: false,
        userName: "",
        userAvatarId: "",
        userIconUrl: "",
        tf_bestscore: 0,
        pf_bestscore: 0,
    }),
    getters: {
        
    },
    mutations: {
        SetUser(state, data) {
            state.isAuthorized = true;
            state.userName = data.username;
            state.userAvatarId = data.userAvatarId;
            state.userIconUrl = data.usericon;
            state.tf_bestscore = data.tfScore;
            state.pf_bestscore = data.pfScore;
        },
        SetAuthToTrue(state) {
            state.isAuthorized = true;
        },
        Logout(state) {
            state.isAuthorized = false;
            state.userName = null;
            state.userAvatarId = null;
            state.userIconUrl = null;
            state.tf_bestscore = null;
            state.pf_bestscore = null;
        },
        SetAvatar(state, data) {
            state.userAvatarId = data.avatarId;
            state.userIconUrl = data.avatarUrl;
        },
        SetUsername(state, username) {
            state.userName = username;
        },
        SetBestscores(state, data) {
            state.tf_bestscore = data.tf_bestscore;
            state.pf_bestscore = data.pf_bestscore;
        }, 
        IncrementTfBestscore(state) {
            state.tf_bestscore = state.tf_bestscore+1;
        },
        IncrementPfBestscore(state) {
            state.pf_bestscore = state.pf_bestscore+1;
        }
    },
    actions: {
        
    },
    namespaced: true,
}