import router from '@/router'
import i18n from '@/i18n.js';
import store from '@/store/index.js'

export function useSetTokens(tokens) {
    $cookies.set("kafferep_accessToken", tokens.accessToken)
    $cookies.set("kafferep_refreshToken", tokens.refreshToken, new Date(tokens.expiresDate * 1000))
}

export function removeTokensAndLogout() {
    $cookies.remove("kafferep_accessToken");
    $cookies.remove("kafferep_refreshToken");

    store.commit('user/Logout');
    router.push({ name: "Home_Debug", params: { lang: i18n.global.locale } });
}
