import axios from "axios";
import createAuthRefreshInterceptor from "axios-auth-refresh";

const instance = axios.create({
  headers: {
    "content-type": "application/json"
  },
  responseType: "json"
});
const refreshAuthLogic = failedRequest =>
  instance
    .post(process.env.VUE_APP_AUTHORITY_REFRESH, null, {
      params: {
        accessToken: $cookies.get("kafferep_accessToken"),
        refreshToken: $cookies.get("kafferep_refreshToken")
      }
    })
    .then(tokenRefreshResponse => {
        $cookies.set("kafferep_accessToken", tokenRefreshResponse.data.accessToken);
        $cookies.set("kafferep_refreshToken", tokenRefreshResponse.data.refreshTokenData.refreshToken, new Date(tokenRefreshResponse.data.refreshTokenData.expiresDate * 1000))

        //console.log("tokens were updated! ");
        failedRequest.response.config.headers["Authorization"] =
            "Bearer " + tokenRefreshResponse.data.accessToken;
        return Promise.resolve();
    })
    .catch(error => {
      //console.log("refresh fail");
      localStorage.setItem("accessToken", null);
      localStorage.setItem("refreshToken", null);
      return Promise.reject(error);
    });
createAuthRefreshInterceptor(instance, refreshAuthLogic);

export default instance;