import { useSetTokens } from '@/services/cookieServices/cookies.js'
import { useLoadAvatar } from '@/services/avatarServices/avatar.js'
import { useLoadBestscores } from '@/services/bestscoresServices/bestscores.js'
import store from '@/store/index.js'
import axios from 'axios';
import i18n from '@/i18n.js';

export async function useLogin(props) {
    if (!props.form.inputUsername || !props.form.inputPassword) {
        props.form.errorText = i18n.global.t('validationErrors.Err_EmptyUserCredentials');
        return;    
    }
    else if(props.form.inputUsername.includes(' ') || props.form.inputPassword.includes(' ')) {
        props.form.errorText = i18n.global.t('validationErrors.Err_WhiteSpacesInUserCredentials');
        return;
    }
    const response = await axios.post(process.env.VUE_APP_AUTHORITY_LOGIN, null, {
        params: {
            username: props.form.inputUsername,
            password: props.form.inputPassword
        }
    })
    .catch((error) => {
        props.form.errorText = error.response.data;
    });

    if(response != null && response.status == 200) {
        useSetTokens({
            accessToken: response.data.accessToken,
            refreshToken: response.data.refreshTokenData.refreshToken,
            expiresDate: response.data.refreshTokenData.expiresDate
        })

        await useLoadAvatar(( ) => { });
        await useLoadBestscores();

        //console.log(response);

        store.commit('user/SetAuthToTrue');
        store.commit('popups/CloseAnyPopup');

    }
}