import axiosInstance from '@/axios/axios.js';
import { onActivated, onMounted, computed, reactive } from 'vue';
import { useLoadBestscores } from '@/services/bestscoresServices/bestscores.js'
import store from '@/store/index.js'
import i18n from '@/i18n.js';

export function useGameMode(props) {

    const GameModeState = reactive({
        data: null,
        selectedArticle: "0",
        rightArticle: "1",
        wrongArticle: "2",
        isHidden: true,
        currentScore: 0,
        highScore: 0,
        isApplyButtonDisabled: true,
        isNextRoundButtonDisabled: true,
        isAppearedBefore: false,
    })


    onActivated(async () => {
        if(!GameModeState.isAppearedBefore) {
            await fetchData();
            GameModeState.isAppearedBefore = true;
        }

        props.setSelectedId();

        if(store.state.user.isAuthorized){
            await useLoadBestscores();
            if(GameModeState.currentScore - props.authorizedBestscore.value > 1){
                GameModeState.currentScore = 0;
            }
        }
    });


    const WrongOrRightText = computed(() => GameModeState.isHidden
    ? "" 
    : (GameModeState.rightArticle != GameModeState.selectedArticle) ? i18n.global.t("nameFirst.Wrong") : i18n.global.t("nameFirst.Right"));

    const WrongOrRightStyle = computed(() => GameModeState.isHidden 
    ? "" 
    : (GameModeState.rightArticle != GameModeState.selectedArticle) ? "wrong" : "right");

    const fetchData = async () => {
        //console.log('fetchData')

        const response = await axiosInstance.get(process.env.VUE_APP_IKEAMIND_API, {
            params: {
                locale: i18n.global.locale
            }
        });
        function getRandomInt(max) {
            return Math.floor(Math.random() * max);
        }
        var randomRight = getRandomInt(3)
        response.data.rightProductID = randomRight;
        GameModeState.rightArticle = response.data.products[randomRight].article;
        GameModeState.data = response.data;
    }

    const productItemClicked = (product) => {
        if(GameModeState.isHidden) {
            GameModeState.selectedArticle = product.article;
            GameModeState.isApplyButtonDisabled = false;
        }
    }

    const applyChoice = () => {
        if((GameModeState.selectedArticle != GameModeState.rightArticle)) {
            GameModeState.wrongArticle = GameModeState.selectedArticle;
            GameModeState.currentScore = 0;
        }
        else {
            if(store.state.user.isAuthorized) {
                incrementAccountScore();
            } else {
                incrementLocalScore();
            }
        }

        GameModeState.isHidden = false;
        GameModeState.isApplyButtonDisabled = true;
        GameModeState.isNextRoundButtonDisabled = false;
    }

    const incrementLocalScore = () => {
        GameModeState.currentScore++;
        if(GameModeState.currentScore > GameModeState.highScore) {
            GameModeState.highScore = GameModeState.currentScore;
        }
    }
    
    const incrementAccountScore = () => {
        GameModeState.currentScore++;
        if(GameModeState.currentScore - props.authorizedBestscore.value > 1){
            GameModeState.currentScore = 0;
            return
        }
        props.incrementAccountScore(GameModeState.currentScore);
    }

    const nextRound = async () => {
        GameModeState.isHidden = true;
        await fetchData();
        GameModeState.isApplyButtonDisabled = true;
        GameModeState.isNextRoundButtonDisabled = true;
    }

    return { GameModeState, WrongOrRightText, WrongOrRightStyle, fetchData, productItemClicked, applyChoice, nextRound }
}