<template>
<div v-if="GameModeState.data != null" id="nameFirstParent">
    <product-head 
    :isDefaultGuy="(GameModeState.rightArticle != GameModeState.selectedArticle) ? GameModeState.isHidden : true" 
    :productName="GameModeState.data.products[GameModeState.data.rightProductID].productName"
    class="productHead"/>

    <div id="productListParent">
        <div id="productList" v-if="GameModeState.data != null">
            <selectable-proudct
            class="selectableProduct"
            v-for="product in GameModeState.data.products"
            :key="product.article"
            :isSelected="GameModeState.selectedArticle === product.article" 
            :productUrl="product.productURL"
            :productCategory="product.productCategory" 
            :productImageLink="product.imageURL" 
            :productName="product.productName"
            :isHidden="GameModeState.isHidden"
            :isRight="GameModeState.rightArticle === product.article"
            :isWrong="GameModeState.wrongArticle === product.article"
            @click="productItemClicked(product)"
            />
        </div>

    </div>

    <div id="WrongOrRight">
        <span :class="WrongOrRightStyle">
            {{WrongOrRightText}}
        </span>
    </div>


    <div id="buttons">
        <button 
        class="blueButton btn" 
        :disabled="GameModeState.isApplyButtonDisabled"
        @click="applyChoice"> 
            {{ $t('nameFirst.CheckIt')}}
        </button>

        <button 
        class="whiteButton btn" 
        :disabled='GameModeState.isNextRoundButtonDisabled'
        @click="nextRound"> 
            {{ $t('nameFirst.Next')}}
        </button>
    </div>

    <div id="statisticParent">
        <div>
            <span class="fontBlue">{{ $t('nameFirst.CurrentScore')}}</span>
            <span>{{GameModeState.currentScore}}</span>
        </div>
        <div>
            <span class="fontBlue">{{ $t('nameFirst.HighScore')}}</span>
            <span>{{isAuth ? authorizedTfBestscore : GameModeState.highScore}}</span>
        </div>
    </div>



</div>  
</template>

<script setup>
import productHead from './components/Header.vue';
import selectableProudct from './components/SelectableProduct.vue';
import { useGameMode } from '@/services/gameModeServices/gameMode.js'
import { useIncrementTitleFirstAccountScore } from '@/services/bestscoresServices/bestscores.js'
import { computed } from 'vue';
import store from '@/store/index.js'

const isAuth = computed(() => store.state.user.isAuthorized);
const authorizedTfBestscore = computed(() => store.state.user.tf_bestscore);

const setSelectedSideMenuId = () => {
    store.commit('menus/setSelectedId', 1);
}

const { GameModeState, WrongOrRightText, WrongOrRightStyle, fetchData, productItemClicked, applyChoice, nextRound } = useGameMode({
    incrementAccountScore: useIncrementTitleFirstAccountScore,
    setSelectedId: setSelectedSideMenuId,
    authorizedBestscore: authorizedTfBestscore,
})

</script>

<style lang="scss" scoped>
@import "@/styles/GameMode/GameMode.scss";

#nameFirstParent{
    display: flex;
    flex-direction: column;
    margin-left: 0px;
}

#productListParent {
    margin: 15px auto 0 auto;
}

#productList {
    display: flex;
    justify-content: space-between;
    width: 600px;
    max-width: 600px;
    flex-direction: row;
}

@media (max-width: 650px) { 
    #productListParent {
        display: flex;
        width: 100vw;
        margin-top: 10px;
    }
    #productList {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: auto;
        margin: 0 auto;
    }
    .selectableProduct {
        margin: 0 auto;
    }
}

</style>