<template>
    <div id="rootPopupParent">
        <div id="RootPopupHeaderParent">
            <div id="RootPopupHeaderText">
                <img class="SmallIcons" :src="popupIconUri" />
                <span id="RootTitleSpan">{{ popupTitleCallback() }}</span>
            </div>
            <div 
            @click="ClosePopup"
            id="RootPopupHeaderClose">
                <img id="closeIcon" class="SmallIcons" src="@/images/icons/close.svg" />
            </div>
        </div>
        <div id="PopupContent">
            <slot />
        </div>
    </div>
</template>

<script setup>
import { computed } from 'vue';
import store from '@/store/index.js'

const ClosePopup = () => {
    store.commit('popups/CloseAnyPopup');
}

const popupTitleCallback = computed(() => store.state.popups.popupTitleCallback);
const popupIconUri = computed(() => store.state.popups.iconUri);

</script>

<style lang="scss" scoped>
span {
    font-family: "Roboto";
}

#RootPopupHeaderParent{
    height: 70px;
    display: flex;
    align-items: center;
    padding: 0px 25px;
    font-size: 15pt;
    font-weight: bold;
    justify-content: space-between;
    border-bottom: 1px solid  #DFDFDF;
}

#RootPopupHeaderText {
    display: flex;
    align-items: center;
}

#RootPopupHeaderClose {
    display: flex;
    align-items: center;
    height: 40px;
    width: 40px;
    cursor: pointer;
    border-radius: 20px;
    &:hover {
        background-color: #E8E8E8;
    }
}

.SmallIcons {
    width: 25px;
    height: 25px;
    margin: 0 auto;
}


#RootTitleSpan{
    margin-left: 10px;
}

#PopupContent {
    display: flex;
    flex-direction: column;
}



</style>