<template>
    <div id="langPopupParent">
        <ul id="langElementParent">
            <li
            v-for="lang in avaliableLangs"
            :key="lang.Id"
            @click="changeLanguage(lang)"
            id="langButtonParent">
                <img id="langButtonFlag" :src="lang.LangFlagName" />
                <span id="langButtonLangName">{{lang.LangName}}</span>
            </li>
        </ul>
    </div>
</template>

<script setup>
import { computed, onMounted } from 'vue';
import router from '@/router'
import i18n from '@/i18n.js';
import store from '@/store/index.js'

const avaliableLangs = computed(() => store.state.language.avaliableLangs);

onMounted(() => {
    store.commit('popups/SetPopupTitleCallback', () => 'Select Language');
});

const changeLanguage = (lang) => {
    i18n.global.locale = lang.LangDomainName;
    router.push({ params: { lang: i18n.global.locale } });
    store.commit('language/SetCurrentLocaleId', lang.Id);
    store.commit('popups/CloseAnyPopup');
}



</script>

<style lang="scss" scoped>
span {
    font-family: "Roboto";
}

#langButtonParent {
    display: flex;
    align-items: center;
    height: 88px;
    border: 1px #DFDFDF;
    border-style: solid none none none;
    padding-left: 25px;
}


#langElementParent{
    cursor: pointer;
    max-height: 330px;
    overflow: auto;
}

#SelectLangSpan{
    margin-left: 10px;
}

#langButtonFlag {
    width: 35px;
    height: 35px;
}

#langButtonLangName {
    font-size: 12pt;
    font-weight: bold;
    margin-left: 15px;
}

</style>