<template>
  <div id="sideMenuParent">
      <div class="topMenus">
          <side-btn 
              v-for="item in menuItems"
              class="pointer"
              :key="item.Id"
              :id="item.Id" 
              @click="buttonClicked(item)"
              :iconUrl="menuIcons[item.Id].url"
              :isSelected="selectedId === item.Id"
              :popUpText="item.Title"/>

        <div @click="AccountClicked" id="accountButtonHorizontalMenu">
          <img 
            class="accountButtonHorizontalIcon pointer" 
            :class="!isAuth ? 'accountButton' : 'avatar ' + (selectedId == 3 ? 'selectedSmallAvatar' : 'unselectedSmallAvatar')" 
            :src="!isAuth ? require(`@/images/icons/account.svg`) : userIconUrl" />
        </div>  

      </div>
      <div class="menuElement">
        <div @click="AccountClicked">
          <img 
            class="bottomIcon pointer" 
            :class="!isAuth ? 'accountButton' : 'avatar ' + (selectedId == 3 ? 'selectedSmallAvatar' : 'unselectedSmallAvatar')" 
            :src="!isAuth ? require(`@/images/icons/account.svg`) : userIconUrl" />
        </div>
        <div @click="ShowLangPopup">
          <img class="bottomIcon pointer" :src="avaliableLangs[currentLocaleId].LangFlagName" />
        </div>
      </div>
  </div>
</template>

<script setup>
import sideBtn from './SideMenuButton.vue'
import { computed, onMounted } from 'vue';
import router from '@/router'
import i18n from '@/i18n.js';
import { useLoadAvatar } from '@/services/avatarServices/avatar.js'
import store from '@/store/index.js'

  // computed properties
  const menuItems = computed(() => store.state.menus.menuItems);
  const menuIcons = computed(() => store.state.menus.menuIcons);
  const selectedId = computed(() => store.state.menus.selectedId);
  const currentLocaleId = computed(() => store.state.language.currentLocaleId);
  const avaliableLangs = computed(() => store.state.language.avaliableLangs);
  const isAuth = computed(() => store.state.user.isAuthorized);
  const userIconUrl = computed(() => store.state.user.userIconUrl);

  const ShowLangPopup = () => {
    store.commit('popups/ShowLangPopup');
  } 

  onMounted(async () => {
    if($cookies.get("kafferep_accessToken") != null) {
      store.commit('user/SetAuthToTrue');
      await useLoadAvatar(( ) => { });
    }
  })

  const buttonClicked = (item) => {
    if(menuItems.value[item.Id].Link !== '' && item.Id != selectedId.value) {
      store.commit('menus/setSelectedId',item.Id);
      router.push({ name: menuItems.value[item.Id].LinkName, params: { lang: i18n.global.locale } });
    }
  }

  const AccountClicked = () => {
    if(!isAuth.value) {
      store.commit('popups/ShowAuthPopup');
    }
    else {
      router.push({ name: "Account", params: { lang: i18n.global.locale } });
    }
  }

</script>

<style lang="scss">
#sideMenuParent {
  width: 100%;
  height: 100%;
  background-color: #fff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.topMenus{
  margin-top: 74px;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.pointer{
  cursor: pointer;
}

.menuElement {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 40px;
  height: 105px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 15px;
}

.bottomIcon {
  width: 40px;
  height: 40px;
}

.accountButton {
  filter: invert(92%) sepia(22%) saturate(4032%) hue-rotate(347deg) brightness(107%) contrast(97%);
  &::selection {
    filter:none;
  }
}

.avatar {
  border-radius: 25px;
  border: 2px solid #00000000; 
  width: 38px;
  height: 38px;
}

.selectedSmallAvatar {
  outline: 3px solid #0058AB;
}

.unselectedSmallAvatar {
  outline: 3px solid #FBD914;
}

#accountButtonHorizontalMenu {
  display: none;
}

@media (max-width: 800px) {
  .topMenus {
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 55px;
    width: 100%;
  }

  .menuElement {
    display: flex;
    width: 35px;
    height: 35px;
    margin: auto 15px auto 0;
    display: none;
  }

  #accountButtonHorizontalMenu {
    width: calc(100vw / 4);
    height: 30px;
    margin-top: auto;
    margin-bottom: auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .accountButtonHorizontalIcon {
    width: 30px;
    height: 30px;
    margin: auto;
  }
}

</style>