import axiosInstance from '@/axios/axios.js';
import store from '@/store/index.js'
import { computed } from 'vue'
import i18n from '@/i18n.js';

export async function useSetUsername(props) {
    if (!props.userData.username) {
        props.errorText = i18n.global.t('validationErrors.Err_EmptyUsername');
        return;    
    }
    else if(props.userData.username.includes(' ')) {
        props.errorText = i18n.global.t('validationErrors.Err_WhiteSpacesInUsername');
        return;
    }

    const response = await axiosInstance.post(process.env.VUE_APP_USER_CONTROLLER + "SetUserName", null, {
        headers: {
            Authorization: "Bearer " + $cookies.get("kafferep_accessToken")
        }, 
        params: { 
            username: props.userData.username
        }
    })
    .catch((error) => {
        if(error.response.status == 400)
            props.errorText = error.response.data;
    });

    if(response != null && response.status == 200) {
        store.commit('user/SetUsername', props.userData.username);
    }
}

export async function useSetAvatar(newAvatar) { 
    const userAvatarId = computed(() => store.state.user.userAvatarId);
    if(newAvatar.avatarId == userAvatarId)
    {
        return;
    }

    const response = await axiosInstance.post(process.env.VUE_APP_USER_CONTROLLER + "SetUserAvatar", null, {
        headers: {
            Authorization: "Bearer " + $cookies.get("kafferep_accessToken")
        }, 
        params: { 
            avatarId: newAvatar.avatarId
        }
    });

    if(response.status == 200) {
        store.commit('user/SetAvatar', {
            avatarId : newAvatar.avatarId,
            avatarUrl: newAvatar.avatarImageUrl
        });
    }
}