import { computed } from 'vue';
export function useSelectableProduct(props) { 
    const currentStyleName = computed(() => {
        if(props.isRight && !props.isHidden) {
            return 'right'
        }
        else if (props.isWrong) {
            return 'wrong'
        }
        else if(props.isSelected) {
            return 'selected'
        }
        else {
            return 'unselected'
        }
    });

    const currentBackgroundName = computed(() => {
        if(props.isRight && !props.isHidden) {
            return 'rightBackground'
        }
        else if (props.isWrong) {            
            return 'wrongBackground'
        }
        else if(props.isSelected) {
            return 'selectedBackground'
        }
        else {
            return 'unselectedBackground'
        }
    });

    return { currentStyleName, currentBackgroundName }
}