<template>
    <div id="authPopupParent">
        <div id="AuthForm">

            <div class="WelcomeSpansHeader">
                <span id="WelcomeMainSpan">Välkommen!</span>
                <span id="WelcomeTranscriptionSpan"><i>{{$t('authPopup.WelcomeTranscription')}}</i></span>
                <span id="WelcomeDescriptionSpan">{{$t('authPopup.WelcomeDescription')}}</span>
            </div>

            <label class="test-input mdc-text-field mdc-text-field--outlined">
                <span class="mdc-notched-outline">
                    <span class="mdc-notched-outline__leading"></span>
                    <span class="mdc-notched-outline__notch">
                        <span class="customFloatLabel mdc-floating-label" id="my-label-id">{{$t('authPopup.Username')}}</span>
                    </span>
                    <span class="mdc-notched-outline__trailing"></span>
                </span>
                <input type="text" v-model="authForm.inputUsername" class="mdc-text-field__input" aria-labelledby="my-label-id">
            </label>

            <label class="test-input-password mdc-text-field mdc-text-field--outlined">
                <span class="mdc-notched-outline">
                    <span class="mdc-notched-outline__leading"></span>
                    <span class="mdc-notched-outline__notch">
                        <span class="customFloatLabel mdc-floating-label" id="my-label-id">{{$t('authPopup.Password')}}</span>
                    </span>
                    <span class="mdc-notched-outline__trailing"></span>
                </span>
                <input type="password" v-model="authForm.inputPassword" class="mdc-text-field__input" aria-labelledby="my-label-id">
            </label>

            <div id="errorMessageParent">
                <error-message :message="authForm.errorText" width="auto" class="errorMessage" isMultiplelineAvaliable="true" />
            </div>
            
            <blue-button @click="isLogin ? useLogin({form: authForm}) : useSignup({form: authForm})" :text="isLogin ? $t('authPopup.LoginButtonText'): $t('authPopup.SignupButtonText')" class="loginButton" />
            
            <div class="GoRegister">
                <span>{{isLogin ? $t('authPopup.DontHaveAccountSpan') : $t('authPopup.HaveAccountSpan') }}</span>
                <a @click="switchFormState">{{isLogin ? $t('authPopup.SwitchToSignup') : $t('authPopup.SwitchToLogin') }}</a>
            </div>
        </div>
    </div>
</template>

<script setup>
import { reactive, ref, onMounted } from 'vue';
import blueButton from "@/components/BlueButton.vue";
import errorMessage from "@/components/ErrorMessage.vue";
import { useLogin } from './services/login.js'
import { useSignup } from './services/signup.js'
import store from '@/store/index.js'
import i18n from '@/i18n.js';


    onMounted(() => {
        store.commit('popups/SetPopupTitleCallback', () => isLogin.value ? i18n.global.t('authPopup.LoginTitle') : i18n.global.t('authPopup.SignupTitle'));
        mdc.textField.MDCTextField.attachTo(document.querySelector('.test-input'));
        mdc.textField.MDCTextField.attachTo(document.querySelector('.test-input-password'));
    });

    const authForm = reactive({
        inputUsername: '',
        inputPassword: '',
        errorText: ''
    });
    
    const isLogin = ref(true);
    const switchFormState = () => {
        isLogin.value = !isLogin.value;
    }

</script>

<style lang="scss" scoped>
span {
    font-family: "Roboto";
}

#AuthForm {
    display: flex;
    flex-direction: column;
    width: 325px;
    margin: 10px auto 0 auto;
}

.WelcomeSpansHeader{
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

#WelcomeMainSpan {
    font-size: 15pt;
    font-weight: 700;
}

#WelcomeTranscriptionSpan{    
    font-size: 13pt;
    font-weight: 500;
}

#WelcomeDescriptionSpan {
    font-size: 12pt;
    font-weight: 400;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
    -webkit-text-fill-color: black;
    transition: background-color 5000s ease-in-out 0s;
}


.form-button,
.test-input,
.test-input-password,
.custom-outlined-button,
.mdc-floating-label,
.customFloatLabel {
    --mdc-theme-primary: #0058AB;
    --mdc-floating-label: #000000;
}

.mdc-floating-label {
    -webkit-text-fill-color: black
}

.mdc-text-field  {
    width: 325px;
    margin-bottom: 20px;
}

.mdc-button {
    width: 64px;
}

.loginButton {
    height: 80px;
}

.GoRegister {
    margin-top: 15px;
    font-weight: 500;
    font-family: "Roboto";
}

#errorMessageParent {
    margin-bottom: 10px;
}

@media (max-width: 800px) {
    .WelcomeSpansHeader, .test-input, .test-input-password, .loginButton, #AuthForm {
        width: 250px;
    }
}

</style>