import axiosInstance from '@/axios/axios.js';
export const NameFirstPageModule = {
    state: () => ({
        data: null,
        selectedArticle: "0",
        rightArticle: "1",
        wrongArticle: "2",
        isHidden: true,
        currentScore: 0,
        highScore: 0,
        isApplyButtonDisabled: true,
        isNextRoundButtonDisabled: true,
        isAppearedBefore: false,
    }),
    getters: {
        
    },
    mutations: {
        setData(state, response) {
            state.data = response;
        },
        setSelectedArticle(state, article) {
            state.selectedArticle = article;
        },
        setRightArticle(state, article) {
            state.rightArticle = article;
        },
        setWrongArticle(state, article) {
            state.wrongArticle = article;
        },
        setHidden(state, isHidden) {
            state.isHidden = isHidden;
        },
        incrementLocalScore(state) {
            state.currentScore++;
            if(state.currentScore > state.highScore) {
                state.highScore = state.currentScore;
            }
        },
        resetScore(state) {
            state.currentScore = 0;
        },
        setApplyButtonState(state, isDisabled) {
            state.isApplyButtonDisabled = isDisabled;
        },
        setNextRoundButtonState(state, isDisabled) {
            state.isNextRoundButtonDisabled = isDisabled;
        },
        appeared(state) {
            state.isAppearedBefore = true;
        }
        
    },
    actions: {
        async incrementAccountScore({state, commit, rootState}) {
            state.currentScore++;
            if(state.currentScore > rootState.user.tf_bestscore) {
                    const response = await axiosInstance.post(process.env.VUE_APP_USER_CONTROLLER + "IncrementBestscore", null, {
                        headers: {
                            Authorization: "Bearer " + $cookies.get("kafferep_accessToken")
                        }, 
                        params: { 
                            mode: "TitleFirst"
                        }
                    })

                    if(response != null && response.status == 200) {
                        commit('user/IncrementTfBestscore', null, { root: true } )
                    }
            }

        }
    },
    namespaced: true,
}