<template>
    <div v-if="userPageState.userData != null" id="userpageParent">
        <div id="userpageContentParent">
            <div id="userheaderParent">
                <img :src="userIconUrl" id="currentAvatar" />
                <div id ="usernameParent">
                    <span id="hej">Hej, </span>
                    <div id="usernameInputParent">
                        <input type="text" id="usernameBig" :value="userPageState.userData.username" @input="onUsernameChange($event.target.value)"/>
                        <div 
                            v-if="userPageState.userData.username != userName" 
                            id="saveNameButton" 
                            @click="useSetUsername(userPageState)"
                        >
                            <img class="SaveIcon" src="@/images/icons/done.svg" draggable="false" /> 
                        </div>
                    </div>
                    <error-message :message="userPageState.errorText" isMultiplelineAvaliable="false" />
                </div>
            </div>

            <separator class="separator"/>

            <div id="avatarChangeParent">
                <span class="titleSpan">{{$t('userPage.YourAvatarTitle')}}</span>
                <div id ="avatarList">
                    <avatar 
                        id="avatarListElement"
                        v-for="avatarModel in userPageState.userData.avatars"
                        :key="avatarModel.avatarId"
                        :avatarImageUrl="avatarModel.avatarImageUrl"
                        :avatarName="avatarModel.avatarName"
                        :avatarDescription="avatarModel.avatarDescriptionByLocales[$i18n.locale]"
                        :isSelected="avatarModel.avatarId == userAvatarId"
                        @click="useSetAvatar(avatarModel)"
                    />
                </div>
            </div>

            <separator/>
            
            <div id="statParent">
                <span class="titleSpan">{{$t('userPage.StatisticsTitle')}}</span>
                <div id="statList">
                    <div>
                        <span class="statElementName">{{$t('userPage.StatisticsTitleFirst')}}</span>
                        <span>{{userPageState.userData.titleFirstBestscore}}</span>
                    </div>
                    <div>
                        <span class="statElementName">{{$t('userPage.StatisticsPictureFirst')}}</span>
                        <span>{{userPageState.userData.pictureFirstBestscore}}</span>
                    </div>
                </div>
            </div>

            <separator/>

            <div id="logoutParent">
                <span class="titleSpan">{{$t('userPage.LogoutTitle')}}</span>
                <div id="logoutButtonParent">
                    <blue-button @click="removeTokensAndLogout" :text="$t('userPage.LogoutButtonText')" id="logoutButton" />
                </div>
            </div>

            <separator/>

        </div>
    </div>  
</template>

<script setup>
import { reactive, computed, onMounted, onActivated } from 'vue';
import avatar from './components/Avatar.vue';
import separator from './components/Separator.vue';
import blueButton from "@/components/BlueButton.vue";
import errorMessage from "@/components/ErrorMessage.vue";
import { useSetUsername, useSetAvatar } from './services/userproperties.js'
import { useUserpage } from './services/userpage.js'
import { removeTokensAndLogout } from '@/services/cookieServices/cookies.js'
import store from '@/store/index.js'

const userIconUrl = computed(() => store.state.user.userIconUrl);
const userAvatarId = computed(() => store.state.user.userAvatarId);
const userName = computed(() => store.state.user.userName);

onActivated(async () => {
    store.commit('menus/setSelectedId', 3); 
    await useUserpage(userPageState);
});

const userPageState = reactive({
    userData: null,
    errorText: ''
});

const onUsernameChange = (newValue) => {
    userPageState.userData.username = newValue;
    userPageState.errorText = "";
}
</script>

<style lang="scss">
span {
    font-family: "Roboto";
}

.separator{
    width: 100%;
    max-width: 100vw;
}

#userpageParent {
    padding: 20px 30px 0px 30px;
}

#userpageContentParent {
    max-width: 890px;
}

#userheaderParent {
    width: auto;
    height: 160px;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
}

#currentAvatar {
    width: 100px;
    height: 100px;
    border: 5px solid #00000000; 
    outline: 5px solid #0058AB;
    border-radius: 100px;
    margin: auto 0 auto 20px;
}

#usernameParent {
    display: flex;
    flex-direction: column;
    margin: auto 0 auto 20px;
}

#hej {
    font-family: "Verdana";
    font-size: 20pt;
}

#usernameBig {
    border: none;
    font-family: "Roboto";
    font-size: 30pt;
    font-weight: 800;
}

.titleSpan {
    font-size: 15pt;
    margin: 0 0 0 20px;
    font-weight: 600;
}

#avatarChangeParent {
    width: auto;
    height: auto;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    max-width: calc(100vw - 30px);
    overflow-x: auto;
    white-space: nowrap;
}

#avatarList {
    width: auto;
    margin: 20px 20px 0 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

#avatarListElement {
    width: 130px;
    cursor: pointer;
    margin: 0 7px;
    white-space: pre-wrap;
}

#statList {
    margin: 15px 0 0 20px;
    font-size: 13pt;
    font-weight: 600;
}

.statElementName {
    color: #0058AB;
}

#usernameInputParent{
    display: flex;
}

#saveNameButton {
    background-color: #0058AB;
    width: 40px;
    height: 40px;
    border-radius: 20px;
    display: flex;
    margin: auto 0 auto 15px;
    cursor: pointer;
}

.SaveIcon {
    width: 30px;
    height: 30px;
    filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(288deg) brightness(102%) contrast(102%);
    &::selection {
        filter:none;
    }
    margin: auto;
}

#logoutButtonParent {
    margin: 20px;
}

#logoutButton {
    width: 100%;
    height: 130px;
    font-size: 14pt;
}

@media (max-width: 840px) {
    #userheaderParent {
        flex-direction: column;
        align-items: center;
        height: auto;
        width: auto;
    }
    #usernameParent {
        width: 100%;
        text-align: center;
        margin-top: 15px;
    }
    #usernameBig{
        width: calc(100% - 40px);
        text-align: center;
        margin: 0 auto;
    }
}

</style>